* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'Prompt', sans-serif;
}
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}
#sButton {
  border: none;
  outline: none;
  width: 100%;
  padding: 15px 0;
  color: black;
  font-size: 16px;
  letter-spacing: 1px;
  background: #70C1AA;
  cursor: pointer;
}
.login {
  width: 100%;
  min-height: 100vh;
  padding: 0 0px;
  background: #99DFB2;
  display: flex;
}
.loginTitleContainer{
  margin-top: 10px;
  padding-bottom: 80px;
}
.loginContainer {
  padding: 40px;
  margin: auto;
  width: 100%;
  max-width: 450px;
  height: 500px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background: white;
  box-shadow: 0 50px 70px -20px rgba(0, 0, 0, 0.8);
  padding-bottom: 200px;
}
.loginLogoContainer {
  margin-top: 50px;
}
.loginlogo {
    width: 100px;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
